import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ProcessorService } from '../shared/processor.service';
import { Processor } from '../shared/processor.model';

@Component({
  selector: 'app-compare2',
  templateUrl: './compare2.component.html',
  styleUrls: ['./compare2.component.css'],
  providers: [ProcessorService]
})
export class Compare2Component implements OnInit {



    constructor(private processorService: ProcessorService) { }

    ngOnInit() {
    }


    onView(_id: string, form: NgForm) {
      this.processorService.getProcessor(_id).subscribe((res) => {
      this.processorService.selectedProcessor = res as Processor;
      });
    }





}

