import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/toPromise';

import { Processor } from './processor.model';

@Injectable()
export class ProcessorService {
  selectedProcessor: Processor;
  processors: Processor[];
  readonly baseURL = 'http://localhost:3000/processors';
  constructor(private http: HttpClient) { }

  postProcessor(pro: Processor) {
    return this.http.post(this.baseURL, pro);
  }

  getProcessorList() {
    return this.http.get(this.baseURL);
  }
  getProcessor(_id: string) {
    return this.http.get(this.baseURL + `/${_id}`);
  }

  putProcessor(pro: Processor) {
    return this.http.put(this.baseURL + `/${pro._id}`, pro);
  }

  deleteProcessor(_id: string) {
    return this.http.delete(this.baseURL + `/${_id}`);
  }
}
