import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { HomeComponent } from './home/home.component';
import { InfoEngComponent } from './info/info-eng/info-eng.component';
import { InfoHinComponent} from './info/info-hin/info-hin.component';
import { InfoTelComponent} from './info/info-tel/info-tel.component';
import { ProcessorsComponent } from './processors/processors.component';
import { InteractiveComponent } from './interactive/interactive.component';
import { FullpcComponent } from './interact/fullpc/fullpc.component';
import { FrontComponent } from './interact/front/front.component';
import { BackcpuComponent } from './interact/backcpu/backcpu.component';
import { InsidecpuComponent } from './interact/insidecpu/insidecpu.component';
import { CompareComponent } from './compare/compare.component';
import { FilterComponent } from './filter/filter.component';
const appRoutes: Routes = [
    { path: 'home', component: HomeComponent },
    { path: 'info-eng', component: InfoEngComponent },
    { path: 'info-hin', component: InfoHinComponent },
    { path: 'info-tel', component: InfoTelComponent },
    { path: 'processors', component: ProcessorsComponent },
    { path: 'interactive', component: InteractiveComponent },
    { path: 'app-fullpc', component: FullpcComponent },
    { path: 'app-front', component: FrontComponent },
    { path: 'app-backcpu', component: BackcpuComponent },
    { path: 'app-insidecpu', component: InsidecpuComponent },
    { path: 'compare', component: CompareComponent },
    { path: 'filter', component: FilterComponent },
];


@NgModule({

  imports: [RouterModule.forRoot(appRoutes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
