import { Component, OnInit } from '@angular/core';
import { NgForm } from '@angular/forms';
import { ProcessorService } from '../shared/processor.service';
import { Processor } from '../shared/processor.model';


declare var M: any;
//  declare var _id: '5cc18a55f623cf448016caf1';

@Component({
  selector: 'app-view-processor',
  templateUrl: './view-processor.component.html',
  styleUrls: ['./view-processor.component.css'],
  providers: [ProcessorService]
})
export class ViewProcessorComponent implements OnInit {

  constructor(public processorService: ProcessorService) { }

  ngOnInit() {

    var id: '5cc18a55f623cf448016caf1';
    this.resetForm();
    this.refreshProcessorList();



  }

  resetForm(form?: NgForm) {
    if(form)
      form.reset();
    this.processorService.selectedProcessor = {
     _id: "",
     pid: "",
     num_of_cores: "",
     l1_Cache: "",
     l2_Cache: "",
     product_code: "",
     microarchitecture: "",
     designed_by: "",
     max_CPU_clock_rate: "",
     min_feature_size: "",
     instruction_set: "",
     predecessor: "",
     successor: "",
     gpu: "",


    }
  }

  onSubmit(form: NgForm) {
    if (form.value._id == "") {
      this.processorService.postProcessor(form.value).subscribe((res) => {
        console.log('43233');
        this.resetForm(form);
        this.refreshProcessorList();
        M.toast({ html: 'Saved successfully', classes: 'rounded' });
      });
    }
    else {
      this.processorService.putProcessor(form.value).subscribe((res) => {
        console.log('hgfgfjh');
        this.resetForm(form);
        this.refreshProcessorList();
        M.toast({ html: 'Updated successfully', classes: 'rounded' });
      });
    }
  }

  refreshProcessorList() {
    this.processorService.getProcessorList().subscribe((res) => {
      this.processorService.processors = res as Processor[];
    });
  }

  onEdit(pro: Processor) {
    this.processorService.selectedProcessor = pro;
  }

  onDelete(_id: string, form: NgForm) {
    if (confirm('Are you sure to delete this record ?') == true) {
      this.processorService.deleteProcessor(_id).subscribe((res) => {
        this.refreshProcessorList();
        this.resetForm(form);
        M.toast({ html: 'Deleted successfully', classes: 'rounded' });
      });
    }
  }

  onView(_id: string, form: NgForm) {
      this.processorService.getProcessor(_id).subscribe((res) => {
        console.log('onView');
      this.processorService.selectedProcessor = res as Processor;
      });
    }



}
